export class FormatDate {
  date: any = "";
  constructor() {}
  result(date: any) {
    this.date = new Date();
    var y = this.date.getFullYear();
    var m = this.date.getMonth() + 1;
    var d = this.date.getDate();
    m = m < 10 ? "0" + m : m;
    d = d < 10 ? "0" + d : d;
    return y + "-" + m + "-" + d;
  }
}
let formatDate = new FormatDate();

